*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    display: flex;
    flex-direction: row-reverse;
    height: 100vh;
}

.bars{
    width: 75vw;
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	justify-content: space-evenly;
    background-color: #353839;
}

.bar{
    width: 10px;
    transition: height 0.2s;
    background-color: gray;
    margin: 0 1px;
}

.sidebar{
    background-color: #555555;
    width: 25vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.header{
    text-align: center;
    font-size: larger;
    color: #fffafa ;
    text-decoration: underline;
}

.algo-box{
    display: flex;
    flex-direction: column;
    /* width: 200px; */
}

h3{
    color: #f0f8ff;
}

.select-option{
    background-color: #c0c0c0;
    color: #555555;
    border-radius: 5px;
    padding: 1.5px;
}

.start-button{
    width: 5em;
    background-color: #B2B2B2;
    color: #3C4048;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 4px;
}

.reset-button{
    width: 8em;
    background-color: #B2B2B2;
    color: #3C4048;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 4px;
}


@media screen and (max-width:485px) {
    .container{
        flex-direction: column;
    }

    .bars{
        width: 100vw;
    }

    .sidebar{
        width: 100vw;
        align-items: stretch;
        padding: 1em;
        /* height: -webkit-fill-available; */
    }

    .options{
        display: flex;
        justify-content: space-between;
        margin-top: 2em;
    }

    .buttons{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .reset-button{
        margin-top: 0.5em;
    }
    
}